@media (max-width: 1440px) {
  .block__btn {
    padding: 12px 26px;
    margin-right: 20px;
    font-size: 15px;
  }
}
@media (max-width: 320px) {
  .block__btn {
    padding: 8px 18px;
    font-size: 11px;
  }
}

#heart,
#bell {
  display: none;
}

#heart-svg,
#bell-svg {
  color: #3670C7;
}
@media (max-width: 1440px) {
  #heart-svg,
  #bell-svg {
    max-width: 33px;
    max-height: 33px;
  }
}
@media (max-width: 320px) {
  #heart-svg,
  #bell-svg {
    max-width: 24px;
    max-height: 24px;
  }
}

.wrapper__card {
  display: grid;
  grid-template-areas: "product block" "description .";
}
@media (max-width: 1440px) {
  .wrapper__card {
    grid-template-areas: "product block" "description description";
  }
}
@media (max-width: 320px) {
  .wrapper__card {
    grid-template-areas: "product " "block" "description ";
  }
}
.wrapper__basket {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}
@media (max-width: 320px) {
  .wrapper__basket svg {
    width: 24px;
    height: 24px;
  }
}
.wrapper__desc {
  display: flex;
  gap: 73px;
  margin-bottom: 33px;
}
.wrapper__delivery, .wrapper__seller {
  display: flex;
  flex-direction: column;
}
.wrapper__sale {
  display: flex;
  align-items: center;
  gap: 13px;
}
@media (max-width: 320px) {
  .wrapper__sale svg {
    width: 14px;
    height: 14px;
  }
}

.delivery__title {
  color: #8F8F8F;
  font-size: 16px;
}
@media (max-width: 320px) {
  .delivery__title {
    font-size: 12px;
  }
}
.delivery__discription {
  color: #2D2D2D;
  font-size: 16px;
}
@media (max-width: 320px) {
  .delivery__discription {
    font-size: 12px;
  }
}

.seller__title {
  color: #8F8F8F;
  font-size: 16px;
}
.seller__discription {
  color: #2D2D2D;
  font-size: 16px;
}

.recommend {
  margin-bottom: 150px;
}
.recommend__title {
  color: #2D2D2D;
  font-size: 50px;
  font-weight: 700;
  line-height: 130%;
}
@media (max-width: 320px) {
  .recommend__title {
    font-size: 20px;
  }
}
.recommend__wrapper {
  display: flex;
  justify-content: space-between;
}